import React, { useState } from 'react';
import BhagvadGita from './BhagvadGita.json';
import moment from 'moment';
import { ToastContainer,toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import wishes from './Wishes.json';
import Moment from 'moment';
import GitaWithMeaning from './BhagvadGita_Meaning.json';
import GitaChapters from './BhagvadGitaChapters.json';
import GitaTelugu from './BhagvadGita_Telugu.json';



export const HomePage = () => {

  
  const today=moment(new Date()).format('MMM DD');
  const [gita,setGita]=useState(BhagvadGita);
  const [gitaWithMean,setGitaWithMean]=useState(GitaWithMeaning);
  const [gitaChapters,setGitaChapters]=useState(GitaChapters);
  const [teluguGita,setTeluguGita]=useState(GitaTelugu);
  let prevData=JSON.parse(window.localStorage.getItem("data"));
  var initialS;
  if(prevData!=null){    
    initialS=prevData.sloka+1;
  }else{
    initialS=1;
  }  
  const [sloka,setSloka]=useState(initialS);
  var vers=gita.filter(verse=>{
    return verse.verse_order==sloka;
  });

  var versWithMean=gitaWithMean.filter(verse=>{
    return (verse.chapter_number==vers[0].chapter_number && verse.verse_number==vers[0].verse_number);
  });

  var verse=vers[0];
  var verseWithMeaning;
  if(versWithMean.length==1){
    verseWithMeaning=versWithMean[0].meaning;
  }

  var teluguVerse=teluguGita.filter(chapter=>{
    return(verse.chapter_number==chapter.chapter_number && verse.verse_number==chapter.verse);
});

  var sub="th";
  if(verse.verse_order==1){
    sub="st";
  }else if(verse.verse_order==2){
    sub="nd";
  }else if(verse.verse_order==3){
    sub="rd";
  }

   const updateCacheforNext=()=>{
    let data={
      sloka:sloka,
      date:new Date()
    };
    window.localStorage.setItem("data",JSON.stringify(data));
    if(sloka==701){
      setSloka(1);
    }else{
      setSloka(sloka+1);
    }   

  }

  const updateCacheforPrevious=()=>{
    let data={
      sloka:sloka,
      date:new Date()
    };
    window.localStorage.setItem("data",JSON.stringify(data));
    if(sloka==1){
      setSloka(701);
    }else{
      setSloka(sloka-1);
    }   
    

  }

  const ih=(data)=>{
    return {
        __html: data    };
}

var audioUrl='/AstroRoops/audio/'+verse.chapter_number+'/'+verse.verse_number+'.mp3';

return (
  <div className='container-fluid mt-3'>
    <div className='card'>
      <div className="card-header gita-header">
        <div >
          <h3 className="card-title">Learn BhagvadGita</h3>
          <h6 className="card-title">(Lets learn a sloka daily.)</h6>
        </div>
        <div>
          
        </div>
        <div>
          <h3 className="card-title">{verse.verse_order}{sub} sloka</h3>
          <h6 className="card-title">{today}</h6>            
        </div>
        </div>
      <div className="card-body"> 
        <div className="card-text">
          <div className="mb-3">                      
           <h3 className="card-text text-primary mb-3" dangerouslySetInnerHTML={ih(verse.text)}></h3>
           <h4 className="card-title text-danger" dangerouslySetInnerHTML={ih(teluguVerse[0].content)}></h4>
         </div>
         <div className="mb-3">                      
           <h4 className="card-text text-success mb-2" dangerouslySetInnerHTML={ih(verse.transliteration)}></h4>
         </div>
         <div className="mb-3">                      
           <h6 className="card-text"  dangerouslySetInnerHTML={ih(verse.word_meanings)}></h6>
         </div>
         <div className="mb-3">
           <h5 className="card-text text-secondary mb-2" dangerouslySetInnerHTML={ih(verseWithMeaning)}></h5>
         </div>
         <div className="hideElement">
            <audio controls  controlsList="nodownload" src={audioUrl} type="audio/mp3" />
        </div>
      </div>
      </div>
    </div>     
    <br></br>
    <div className="form-group mb-3 gita-header">    
      <button className="btn btn-primary w-100 p-2" onClick={updateCacheforPrevious}>previous</button>
      <span className="w-100 p-2"></span>
      <button className="btn btn-primary w-100 p-2" onClick={updateCacheforNext}>Next</button>
    </div>
    
    
  </div>
)

}
