import React, { useState } from 'react'
import { useEffect } from 'react'
import loc from './Location.json'
import Moment from 'moment';
import Clock from 'react-live-clock';
import Geolocation from 'react-native-geolocation-service';
import { ToastContainer,toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import DateTimePicker from 'react-datetime-picker';
import 'react-datetime-picker/dist/DateTimePicker.css';
import 'react-calendar/dist/Calendar.css';
import 'react-clock/dist/Clock.css';

export const CurrentStatus = () => {

    const locationList=['-','Bantumilli','Vijayawada','Hyderabad','Bangalore','Chennai','Palakollu','Sringeri','Kasi','New Delhi','Mumbai','Gaziabad'];
    const days=['Sunday','Monday','Tuesday','Wednesday','Thursday','Friday ','Saturday'];
    const hoursList=['00','01','02','03','04','05'];
    const [loca,setLoca]=useState(loc);
    const[date,setDate]=useState(new Date());
    var SunCalc = require('suncalc');    
    var dat = new Date(date).toISOString();
    const[latitude,setLatitude]=useState(16.43332);
    const[longitude,setLongitude]=useState(81.00207);
    const[location,setLocation]=useState(0);
    const day=new Date(date).getDay();
    useEffect(() => {
      getLocation();
   }, []);
   const getTime = (f) =>{
    const d = new Date(f);
    return Moment(d).format('LT');    
  };
  const getDateFormatted = (f) =>{
    const d = new Date(f);
    return Moment(d).format('DD MMM');    
  };
 
  const getDate = (f) =>{
    const d = new Date(f);
    return Moment(d).format('MMM DD');    
  };  
  
  const ifIsInBetween=(start,end,dat)=>{
    const isInBetween=Moment(dat).isBetween(Moment(start),Moment(end));
    let ifIsInBtwn="N";            
    if(isInBetween){
      ifIsInBtwn="Y";
    }else{
      ifIsInBtwn="N";                
    }
    return ifIsInBtwn;
}

const getList=(vari,index)=>{
    return (
      <option value={index} key={index}>{vari}</option>
    )
  };
  const getLocation =() =>{
    Geolocation.getCurrentPosition((position)=>{
      setLatitude(position.coords.latitude);
      setLongitude(position.coords.longitude);
      setLocation(0);
    },(error)=>{
      console.log(error.code, error.message);
    },{
      enableHighAccuracy:true,timeout:15000,maximumAge:10000
    });
    
  };
  const Times=SunCalc.getTimes(new Date(date),latitude,longitude);
  const MoonTimes=SunCalc.getMoonTimes(new Date(date),latitude,longitude);

  const moonSet=()=>{
    let moonRiseDt=MoonTimes.rise;
    let moonSetDt=MoonTimes.set;
    if(Moment(new Date(moonRiseDt)).isAfter(Moment(new Date(moonSetDt))))
    {
      const tomorrow=new Date(date);
      tomorrow.setDate(tomorrow.getDate()+1);
      let MoonTimesNextDay=SunCalc.getMoonTimes(new Date(tomorrow),latitude,longitude);
      //const mSet=getDateFormatted(MoonTimesNextDay.set) +" ("+getTime(MoonTimesNextDay.set)+")";
      const mSet=MoonTimesNextDay.set;
      return mSet;
    }else{
      console.log("3");
      //const mSet=getDateFormatted(MoonTimes.set) +" ("+getTime(MoonTimes.set)+")";
      const mSet=MoonTimes.set;
      return mSet;
    }
  }

  const onChangeLocationHandler=(e)=>{
    setLocation(e.target.value);      
    const localLoc=loca[e.target.value];
    setLatitude(localLoc.latitude);
    setLongitude(localLoc.longitude);
      };

      const onChangeDateHandler=(e)=>{
        setDate(e.target.value);
      };

  return (

    <div className="container-fluid">
    <div className="dropdown  mb-3 w-100">                                              
       <select className="form-select" aria-labelledby="location" id="location" defaultValue={location} onChange={onChangeLocationHandler} required>
           {locationList.map((loc,index)=>getList(loc,index))}                    
       </select>
   </div>
   <div className="form-group mb-3">
             <button className="btn btn-primary w-100 p-3" onClick={getLocation}>Auto Detect</button>
           </div>
   <div  className="form-group mb-3 w-100">
       
       <DateTimePicker onChange={onChangeDateHandler} value={date } />
         
   </div>

   <div className="row">
        <div className="container-fluid">
            <div className="card">
                 <div className="card-header custom-card-header gita-header">
                    <div className="card-title">
                        <h5 className="">{getDate(date)}</h5>                        
                    </div>      
                    <div className="card-title text-primary">
                        <h5><Clock format={'HH:mm:ss'} ticking={true} timezone={'Asia/Kolkata'} /></h5>
                        <div className="custom-center"><h6 >{locationList[location]}</h6></div>
                    </div>              
                    <div className="card-title text-secondary">
                        <h4 className="">{days[new Date(date).getDay()]}</h4>                          
                    </div>                    
                 </div> 
            </div>
        </div>
        </div>



   </div>

  )

}