import React, { useState } from 'react'
import { useEffect } from 'react'
import loc from './Location.json'
import Moment from 'moment';
import Clock from 'react-live-clock';
import Geolocation from 'react-native-geolocation-service';
import { ToastContainer,toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFaceFlushed, faFaceSmileBeam, faMoon as faMoonRegular,faSmileBeam,faSmileWink,faSun as faSunRegular } from '@fortawesome/free-regular-svg-icons';
import { faCloudMoon, faMoon as faMoonSolid,faSmile,faSun as faSunSolid } from '@fortawesome/free-solid-svg-icons';
import Alert from 'react-bootstrap/Alert';

export const Day = () => {
   // const locationList=['బంటుమిల్లి','విజయవాడ','హైదరాబాద్','పాలకొల్లు','శృంగేరి'];
   const locationList=[' ','Bantumilli','Vijayawada','Hyderabad','Bangalore','Chennai','Palakollu','Sringeri','Kasi','New Delhi','Mumbai','Gaziabad'];
    //const days=['ఆదివారం','సోమవారం','మంగళవారం','బుధవారం','గురువారం','శుక్రవారం','శనివారం'];
    const days=['Sunday','Monday','Tuesday','Wednesday','Thursday','Friday ','Saturday'];
    const [loca,setLoca]=useState(loc);
    const rahuKalamMultiplier=[7,1,6,4,5,3,2];
    const gulikaMultiplier=[6,4,5,3,2,1,0];
    const yamakantakaMultiplier=[4,3,2,1,0,6,5];
    const durmuhurthamstartAdder=[37440,23040,8640,20160,14400,8640,0];
    const durmuhurthamEndAdder=[2880,2880,2880,2880,2880,2880,5760];
    const punarDurmuhurthamstartAdder=[0,31680,20160,0,31680,31680,0];
    const punarDurmuhurthamEndAdder=[0,2880,2880,0,2880,2880,0];
    let locat=" ";

  
    var SunCalc = require('suncalc');
    
    const[date,setDate]=useState(new Date());
    var month=new Date(date).getMonth()+1;
    var mm="";
    var da=new Date(date).getDate();
    var dd="";
    if(month<10){
      mm="0"+month;
    }    else{
      mm=month;
    }
    if(da<10){
      dd="0"+da;
    }    else{
      dd=da;
    }
    //var dat = new Date(date).toISOString().substr(0,10);
    var dat=new Date(date).getFullYear()+"-"+mm+"-"+dd;
    const[latitude,setLatitude]=useState(16.43332);
    const[longitude,setLongitude]=useState(81.00207);
    const[location,setLocation]=useState(0);
    useEffect(() => {
      getLocation();
   }, []);
   const locationValue=()=>{
    locat= locationList[location];
    if(location==0){
        locat="Current Location"; 
    }            
    return locat;
}

    const getTime = (f) =>{
        const d = new Date(f);
        return Moment(d).format('LT');    
      };
      const getDateFormatted = (f) =>{
        const d = new Date(f);
        return Moment(d).format('DD MMM');    
      };
     
      const getDate = (f) =>{
        const d = new Date(f);
        return Moment(d).format('MMM DD');    
      };    

    const getList=(vari,index)=>{
        return (
          <option value={index} key={index}>{vari}</option>
        )
      };
      let locatCounter=0;
      const successMessage=()=>{
        if(locatCounter==1){
          toast.success("Current location detected!", {position: toast.POSITION.BOTTOM_CENTER});
        }
      }
  
      const errorMessage=()=>{
        toast.error("Can't fetch Current location!", {position: toast.POSITION.BOTTOM_CENTER});
      }
      
      const getLocation =() =>{
       // console.log();
        Geolocation.getCurrentPosition((position)=>{
          setLatitude(position.coords.latitude);
          setLongitude(position.coords.longitude);
          setLocation(0);
          locatCounter=locatCounter+1;
          //alert("Location detected!");
          successMessage();
          locat="Current Location";
        },(error)=>{
         // console.log(error.code, error.message);
          errorMessage();
        },{
          enableHighAccuracy:true,timeout:15000,maximumAge:10000
        });
        
      };

    
   

      const onChangeLocationHandler=(e)=>{
        setLocation(e.target.value);      
        const localLoc=loca[e.target.value];
        setLatitude(localLoc.latitude);
        setLongitude(localLoc.longitude);
          };

          const onChangeDateHandler=(e)=>{
            setDate(e.target.value);
          };

          const ifIsInBetween=(start,end)=>{
            const isInBetween=Moment(new Date()).isBetween(Moment(start),Moment(end));
            let body_Color="";            
            if(isInBetween){
              body_Color="text-warning";
            }else{
              body_Color="";                
            }
            return body_Color;
        }
          const Times=SunCalc.getTimes(new Date(date),latitude,longitude);
          const MoonTimes=SunCalc.getMoonTimes(new Date(date),latitude,longitude);
          const moonSet=()=>{
            let moonRiseDt=MoonTimes.rise;
            
            let moonSetDt=MoonTimes.set;
            
            if(moonSetDt==undefined){
              moonSetDt=null;              
            }
            
            if(Moment(new Date(moonRiseDt)).isAfter(Moment(new Date(moonSetDt))))
            {
              const tomorrow=new Date(date);
              tomorrow.setDate(tomorrow.getDate()+1);
              let MoonTimesNextDay=SunCalc.getMoonTimes(new Date(tomorrow),latitude,longitude);
              //const mSet=getDateFormatted(MoonTimesNextDay.set) +" ("+getTime(MoonTimesNextDay.set)+")";
              const mSet=MoonTimesNextDay.set;
              return mSet;
            }else{
              
              //const mSet=getDateFormatted(MoonTimes.set) +" ("+getTime(MoonTimes.set)+")";
              const mSet=MoonTimes.set;
              return mSet;
            }
          }
         
          const moonIllu=SunCalc.getMoonIllumination(new Date(date));
          
     //  console.log("Angle:"+moonIllu.angle);
         // console.log("Phase:"+moonIllu.phase);
         
          // console.log("Fraction:"+moonIllu.fraction);
          const moonPhase=()=>{
            const moonPhaseMultiplier=15;
            const moonAngle=moonIllu.angle;
            const fraction=moonIllu.fraction;
            let thithi=0;
            if(moonAngle>0){//towards new moon              
//              console.log("Thithi:"+(1-fraction)*moonPhaseMultiplier);
              thithi=(1-fraction)*moonPhaseMultiplier;
              thithi=Math.ceil(thithi);
            }else{//towards full moon
             // console.log("Thithi:"+(fraction)*moonPhaseMultiplier);
              thithi=(fraction)*moonPhaseMultiplier;
              thithi=Math.ceil(thithi);
            }
            return thithi;
          }



          const sunposition=SunCalc.getPosition(new Date(date),latitude,longitude);
          const moonposition=SunCalc.getMoonPosition(new Date(date),latitude,longitude);

          
        
          var sunAngle=sunposition.azimuth * 180 / Math.PI;   
          var moonAngle=moonposition.azimuth * 180 / Math.PI;          
          // if(sunAngle<0){
          //   sunAngle=(sunAngle*-1)-24;
          // }
          // if(moonAngle<0){
          //   moonAngle=(moonAngle*-1)-24;
          // }         

          //console.log("Date:"+date);
          //console.log("Latitude:",latitude);
          //console.log("Longitude:",longitude);
          //console.log("Altitude:"+sunposition.altitude);
          //console.log("Sun Azimuth:"+sunposition.azimuth);
          //console.log("Sun Angle:"+sunAngle);
          //console.log("Moon:"+moonposition.parallacticAngle);
          
          
          const day=new Date(date).getDay();
          const rkMultiplier=rahuKalamMultiplier[day];
          const gkMultiplier=gulikaMultiplier[day];
          const ymMultiplier=yamakantakaMultiplier[day];
          const totalTimeDiffByEight=(new Date(Times.sunset).getTime()-new Date(Times.sunrise).getTime())/8;
          const rahuStartTime=new Date(new Date(Times.sunrise).getTime()+(rkMultiplier*totalTimeDiffByEight));
          const rahuEndTime=new Date(new Date(Times.sunrise).getTime()+((rkMultiplier+1)*totalTimeDiffByEight));
          const gkStartTime=new Date(new Date(Times.sunrise).getTime()+(gkMultiplier*totalTimeDiffByEight));
          const gkEndTime=new Date(new Date(Times.sunrise).getTime()+((gkMultiplier+1)*totalTimeDiffByEight));
          const ymStartTime=new Date(new Date(Times.sunrise).getTime()+(ymMultiplier*totalTimeDiffByEight));
          const ymEndTime=new Date(new Date(Times.sunrise).getTime()+((ymMultiplier+1)*totalTimeDiffByEight));
          const dmStAdder=durmuhurthamstartAdder[day];
          const dmEndAdder=durmuhurthamEndAdder[day];
          const durmuhurthaStart=new Date(new Date(Times.sunrise).getTime()+(dmStAdder*1000));
          const durmuhurthaEnd=new Date(durmuhurthaStart.getTime()+(dmEndAdder*1000));
          var punarDurmuhurthaExists="hideElement";
          var punarDurMuhurthamStartTime;
          var punarDurMuhurthamEndTime;
          if(day===1 || day===2 || day===4 ||day===5){
            punarDurmuhurthaExists="card";
            const pdmstAdder=punarDurmuhurthamstartAdder[day];
            const pdmenAdder=punarDurmuhurthamEndAdder[day];
            if(day===2){
                punarDurMuhurthamStartTime=new Date(new Date(Times.sunset).getTime()+(1000*pdmstAdder));
                punarDurMuhurthamEndTime=new Date(new Date(punarDurMuhurthamStartTime).getTime()+(1000*pdmenAdder));
            }else{
                punarDurMuhurthamStartTime=new Date(new Date(Times.sunrise).getTime()+(1000*pdmstAdder));
                punarDurMuhurthamEndTime=new Date(new Date(punarDurMuhurthamStartTime).getTime()+(1000*pdmenAdder));
            }

          }
          

          

  return (
    <div className="container-fluid">
         <div className="dropdown  mb-3 w-100">                                              
            <select className="form-select" aria-labelledby="location" id="location" defaultValue={location} onChange={onChangeLocationHandler} required>
                {locationList.map((loc,index)=>getList(loc,index))}                    
            </select>
        </div>
        <div className="form-group mb-3">
                  <button className="btn btn-primary w-100 p-3" onClick={getLocation}>Auto Detect</button>
                </div>
        <div className="form-floating mb-3" data-provide="datepicker">
            <input  type="date"  id="date"  className="form-control w-100 p-3"  name="date" defaultValue={dat} required onChange={onChangeDateHandler}/>                  
                <div className="input-group-addon">
                   <span className="glyphicon glyphicon-th"></span>
                </div>
        </div>

        <div className="row">
        <div className="container-fluid">
            <div className="card">
                 <div className="card-header custom-card-header gita-header">
                    <div className="card-title">
                        <h5 className="">{getDate(date)}</h5>                        
                    </div>      
                    <div className="card-title text-primary">
                        <h5><Clock format={'HH:mm:ss'} ticking={true} timezone={'Asia/Kolkata'} /></h5>
                        <div className="custom-center"><h6 >{locationValue()}</h6></div>
                    </div>              
                    <div className="card-title text-secondary">
                        <h4 className="">{days[new Date(date).getDay()]}</h4>                          
                    </div>                    
                 </div> 
            </div>
        </div>
        </div>
        <div  className="row">
        <div className="container-fluid">
             <div className="card">  
                 <div className="card-header custom-card-body-header">
                  <h5 className="card-title "><FontAwesomeIcon icon={faSunSolid} className="custom-fav-icon"></FontAwesomeIcon> Sun                  
                  </h5>
                 </div>
                 <div className="card-body text-primary">            
                    <div className="custom-card-body card-text">          
                        <div className={ifIsInBetween(Times.sunrise,Times.sunset)}>
                            <p className="card-text">Rise</p>                 
                            <p className="card-text">{getTime(Times.sunrise)}</p>
                         </div>
                        <div className={ifIsInBetween(Times.sunrise,Times.sunset)}>
                            <p className="card-text custom-text-right">Set</p>                 
                            <p className="card-text">{getTime(Times.sunset)}</p>
                        </div>
                    </div>
                </div>

                <div className="card">  
                 <div className="card-header custom-card-body-header">
                  <h5 className="card-title"><FontAwesomeIcon icon={faCloudMoon} className="custom-moon-icon"></FontAwesomeIcon> Moon</h5></div>
                 <div className="card-body text-primary">            
                    <div className="custom-card-body card-text">          
                    <div className={ifIsInBetween(MoonTimes.rise,moonSet())}>
                            <p className="card-text">Rise</p>                 
                            <p className="card-text">{getDateFormatted(MoonTimes.rise)} ( {getTime(MoonTimes.rise)})</p>
                        </div>
                   
                        <div className={ifIsInBetween(MoonTimes.rise,moonSet())}>
                            <p className="card-text custom-text-right">Set</p>                 
                            <p className="card-text">{getDateFormatted(moonSet())} ({getTime(moonSet())})</p>
                        </div>
                    </div>
                </div>


                <div className="card">  
                 <div className="card-header custom-card-body-header text-danger"><h5 className="card-title">Raahu Kaalam</h5></div>
                 <div className="card-body text-danger">            
                    <div className="custom-card-body card-text">          
                         <div className={ifIsInBetween(rahuStartTime,rahuEndTime)}>
                            <p className="card-text">Start</p>                 
                            <p className="card-text">{getTime(rahuStartTime)}</p>
                         </div>
                         <div className={ifIsInBetween(rahuStartTime,rahuEndTime)}>
                            <p className="card-text custom-text-right">Ends</p>                 
                            <p className="card-text">{getTime(rahuEndTime)}</p>
                        </div>
                    </div>
                </div>
                </div>


                <div className="card">  
                 <div className="card-header custom-card-body-header"><h5 className="card-title">Gulika Kaalam</h5></div>
                 <div className="card-body text-primary">            
                    <div className="custom-card-body card-text">          
                    <div className={ifIsInBetween(gkStartTime,gkEndTime)}>
                            <p className="card-text">Start</p>                 
                            <p className="card-text">{getTime(gkStartTime)}</p>
                         </div>
                         <div className={ifIsInBetween(gkStartTime,gkEndTime)}>
                            <p className="card-text custom-text-right">Ends</p>                 
                            <p className="card-text">{getTime(gkEndTime)}</p>
                        </div>
                    </div>
                </div>
                </div>

                <div className="card">  
                 <div className="card-header custom-card-body-header"><h5 className="card-title">Yamakantaka Kaalam</h5></div>
                 <div className="card-body text-primary">            
                    <div className="custom-card-body card-text">          
                    <div className={ifIsInBetween(ymStartTime,ymEndTime)}>
                            <p className="card-text">Start</p>                 
                            <p className="card-text">{getTime(ymStartTime)}</p>
                         </div>
                         <div className={ifIsInBetween(ymStartTime,ymEndTime)}>
                            <p className="card-text custom-text-right">Ends</p>                 
                            <p className="card-text">{getTime(ymEndTime)}</p>
                        </div>
                    </div>
                </div>
                </div>

                <div className="card">  
                 <div className="card-header custom-card-body-header text-danger"><h5 className="card-title">Durmuhurtham</h5></div>
                 <div className="card-body text-danger">            
                    <div className="custom-card-body card-text">          
                    <div className={ifIsInBetween(durmuhurthaStart,durmuhurthaEnd)}>
                            <p className="card-text">Start</p>                 
                            <p className="card-text">{getTime(durmuhurthaStart)}</p>
                         </div>
                         <div className={ifIsInBetween(durmuhurthaStart,durmuhurthaEnd)}>
                            <p className="card-text custom-text-right">Ends</p>                 
                            <p className="card-text">{getTime(durmuhurthaEnd)}</p>
                        </div>
                    </div>
                </div>
                </div>

                <div className={punarDurmuhurthaExists}>  
                 <div className="card-header custom-card-body-header text-danger"><h5 className="card-title">Punar Durmuhurtham</h5></div>
                 <div className="card-body text-danger">            
                    <div className="custom-card-body card-text">          
                    <div className={ifIsInBetween(punarDurMuhurthamStartTime,punarDurMuhurthamEndTime)}>
                            <p className="card-text">Start</p>                 
                            <p className="card-text">{getTime(punarDurMuhurthamStartTime)}</p>
                         </div>
                         <div className={ifIsInBetween(punarDurMuhurthamStartTime,punarDurMuhurthamEndTime)}>
                            <p className="card-text custom-text-right">Ends</p>                 
                            <p className="card-text">{getTime(punarDurMuhurthamEndTime)}</p>
                        </div>
                    </div>
                </div>
                </div>


            </div>

        </div>


        </div>

</div>
<ToastContainer  autoClose={1000} />
    </div>
  )
}
