import React, { useState } from 'react';
import Gita from './BhagvadGita.json';
import GitaChapter from './BhagvadGitaChapters.json';
import GitaWithMeaning from './BhagvadGita_Meaning.json';
import GitaTelugu from './BhagvadGita_Telugu.json';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faHeart as faRegularHeart } from '@fortawesome/free-regular-svg-icons';
import { faHeart as faSolidHeart } from '@fortawesome/free-solid-svg-icons';



export const BhagvadGita = () => {
    const[gita,setGita]=useState(Gita);   
    const[filteredGita,setFilteredGita]=useState(Gita);
    const [chapterList,setChapterList]=useState(GitaChapter);
    const [gitaWithMean,setGitaWithMean]=useState(GitaWithMeaning);
    const [teluguGita,setTeluguGita]=useState(GitaTelugu);
    const [selectedChapter,setSelecedChapter]=useState(0);
    const [ch,setCh]=useState(chapterList[0]);
    const [isFav,setIsSetFav]=useState(false);
    const [favLabel,setFavLabel]=useState("Click here to show Favorites");

    let favoriteList=JSON.parse(window.localStorage.getItem("favariteList"));  

    const onChangeHandler=(e)=>{
        var dat;
        if(favoriteList!=null){
            dat=favoriteList.data;
        }
        setSelecedChapter(0);
        setCh(chapterList[0]);
        if(e.target.checked){
            setFavLabel("My Favorites");   
            setFilteredGita(Gita.filter(chapter=>{                                       
                var favr=chapter.chapter_number+'@'+chapter.verse_number+'|';
                if(dat.includes(favr)){
                    return chapter;
                }            
            }));
        }else{
            setFavLabel("Click here to show Favorites");
            setFilteredGita(Gita);
        }
       

       

   }

    
    
    const ih=(data)=>{
        return {
            __html: data    };
    }

 

    const getVerse=(verse)=>{         
        

        var chap=gitaWithMean.filter(chapter=>{
            return (verse.chapter_number==chapter.chapter_number && verse.verse_number==chapter.verse_number);
        });
        var teluguVerse=teluguGita.filter(chapter=>{
            return(verse.chapter_number==chapter.chapter_number && verse.verse_number==chapter.verse);
        });
        var verseMeaning="";
        if(chap.length==1){
            verseMeaning=chap[0].meaning;
        }else{
            //console.log("Chapter ",verse.chapter_number, " Verse ", verse.verse_number);
            
        }
        var audioUrl='/AstroRoops/audio/'+verse.chapter_number+'/'+verse.verse_number+'.mp3';
        
         
        var favr=verse.chapter_number+'@'+verse.verse_number+'|';
        var favorText="Add to Favorites";
        var isFavorite=false;
        var regularStyle="";
        var solidStyle="hideElement";

        if(favoriteList!=null){
            var dat=favoriteList.data;
            if(dat.includes(favr)){
                favorText="Remove from Favorites";
                isFavorite=true;
                regularStyle="hideElement";
                solidStyle="";
            }
        }


        return(
        <div className='card' key={verse.verse_order}>            
            <div className="card-header">                
                <h3 className="card-title text-primary" dangerouslySetInnerHTML={ih(verse.text)}></h3>
                <div className="custom-fav-icon">                     
                     <FontAwesomeIcon icon={faRegularHeart} onClick={(e)=>addtoFavorites(e,favr,isFavorite)} className={regularStyle}></FontAwesomeIcon>
                     <FontAwesomeIcon icon={faSolidHeart} onClick={(e)=>addtoFavorites(e,favr,isFavorite)} className={solidStyle}></FontAwesomeIcon>
                </div>
                <h4 className="card-title text-danger" dangerouslySetInnerHTML={ih(teluguVerse[0].content)}></h4>
            </div>
            <div className="card-body"> 
                <div className="card-text">                   
                    <div className="mb-3">                      
                        <h4 className="card-text text-success" dangerouslySetInnerHTML={ih(verse.transliteration)}></h4>
                    </div>
                    <div className="mb-2">                      
                        <h6 className="card-text" dangerouslySetInnerHTML={ih(verse.word_meanings)}></h6>
                    </div>
                    <div className="mb-2">
                        <h5 className="card-text text-secondary" dangerouslySetInnerHTML={ih(verseMeaning)}></h5>
                    </div>
                    {/* <div className="hideElement">
                        <audio controls  controlsList="nodownload" src={audioUrl} type="audio/mp3" />
        </div>  */}
                </div>
            </div>
      </div> 
        )
    };

    const addtoFavorites=(e,favr,isFavorite)=>{
        e.preventDefault();       
        if(!isFavorite){
            if(favoriteList!=null){
                var dat=favoriteList.data+favr;                
                let favrList={
                    data: dat
                };
                window.localStorage.setItem("favariteList",JSON.stringify(favrList));
            }else{                
                let favrList={
                    data: favr
                };
                window.localStorage.setItem("favariteList",JSON.stringify(favrList));
            }
        }else{
            var dat=favoriteList.data;
            dat=dat.replace(favr,'');
            let favrList={
                data: dat
            };
            window.localStorage.setItem("favariteList",JSON.stringify(favrList));

        }
        setIsSetFav(!isFav);
    };

    const getChapterList=(chapter,index)=>{
        return (
          <option value={chapter.chapter_number} key={chapter.chapter_number}>{chapter.name_translation} ({chapter.chapter_number})</option>
        )
      };
      const onChangeChapterHandler=(e)=>{
        setSelecedChapter(e.target.value);  

        var chapter=chapterList[e.target.value];              
        setCh(chapter);
        if(e.target.value==0){
            setFilteredGita(Gita);
        }else{            
            setFilteredGita(Gita.filter(chapter=>{
                var targetValue=e.target.value;                             
                return chapter.chapter_id==targetValue;
            }));
                }

        
      };
  
  return (
    <div className="container-fluid mt-3">
        <div className="card">
            <div className="card-header">
                <div>
                    <h3 className="card-title">BhagvadGita</h3>
                </div>
                { <div>
                    <div className="dropdown  mb-3 w-100 mt-3">                 
                        <label className=""></label>                
                         <select className="form-select" aria-labelledby="chapter" onChange={onChangeChapterHandler}>
                            {chapterList.map((chapter,index)=>getChapterList(chapter,index))}                    
                         </select>
                    </div>

                    <div class="form-check form-switch m-3">
                         <input class="form-check-input" type="checkbox" id="flexSwitchCheckChecked" onChange={onChangeHandler}/>
                         <label class="form-check-label">{favLabel}</label>
                     </div>


                </div> }
            </div>
            <div className="card-header">
                {favLabel=="My Favorites" ?
                <div>
                    <h4 className="card-title text-danger text-center">My Favorites</h4>               
                </div>

                :
                <div>
                {ch.chapter_number==0 ?
                    <div>
                    <h4 className="card-title text-danger text-center">
                         {ch.name_translation} ({ch.verses_count} slokas)
                    </h4>               
                    </div>
                    :
                    <div>
                    <h4 className="card-title text-danger text-center">
                       {ch.chapter_number}. {ch.name_translation} ({ch.verses_count})
                    </h4>
                    <h6 className="card-title text-primary text-center">
                       ({ch.name_meaning})
                    </h6>
                    <h5 className="card-title text-secondary">
                        {ch.chapter_summary}
                    </h5>
                    </div>
                    }
    </div>

                    
                }

               


            </div>
           
        <div className="card-body">
            {filteredGita.map(verse=>getVerse(verse))}
        </div>

        </div>
    </div>
  )
}
